import './App.css';
import {Suspense, useEffect, useRef, useState} from 'react'
import {Canvas} from '@react-three/fiber'
import Model from "./avatar/Model";
import {Html, useProgress} from '@react-three/drei'
import {postMsg} from "./remote/Msger";
import {WebGLRenderer} from "three";

function Loader() {
    const {progress} = useProgress()
    return <Html style={{width: '100px', color: 'gray'}} center>{progress} %</Html>
}

export default function App() {
    const canvas = useRef(null);
    const [url, setUrl] = useState('');
    const [rotateEnabled, setRotateEnabled] = useState(false);
    const avatar = useRef();
    const originX = useRef();
    // const originY = useRef();
    const originAX = useRef();
    // const originAY = useRef();

    useEffect(() => {
        window.setUrl = setUrl;
        window.platform || setUrl('https://d1a370nemizbjq.cloudfront.net/173af6ab-1bee-44ff-a511-2eaf9d0f5a2f.glb')
        window.canvas = canvas;
        window.avatar = avatar;

        window.capture = () => {
            const {height, width} = canvas.current;
            let imgData = canvas.current.toDataURL({
                format: 'image/png',
                quality: .5,
                width: width / devicePixelRatio,
                height: height / devicePixelRatio,
            });
            window.platform ?
                postMsg({type: 'capture', content: imgData}) : console.log(imgData);
        }
    }, [])

    function pdHandler(e) {
        if (avatar.current) {
            originX.current = e.clientX;
            // originY.current = e.clientY;
            originAX.current = avatar.current.rotation.y;
            // originAY.current = avatar.current.rotation.x;
            setRotateEnabled(true);
        }
    }

    function pmHandler(e) {
        e.clientX ||= e.nativeEvent.changedTouches[0].clientX;
        // e.clientY ||= e.nativeEvent.changedTouches[0].clientY;
        rotateEnabled && avatar.current && (avatar.current.rotation.y = originAX.current + (e.clientX - originX.current) / 100);
        // rotateEnabled && avatar.current && (avatar.current.rotation.x = originAY.current + (e.clientY - originY.current) / 100);
    }

    function puHandler() {
        setRotateEnabled(false);
    }

    return (<div className="App">
        <Canvas ref={canvas}
                gl={canvas => new WebGLRenderer({canvas, preserveDrawingBuffer: true})}
                onPointerDown={pdHandler}
                onPointerUp={puHandler}
                onTouchMove={pmHandler}
                onPointerMove={window.platform || pmHandler} // android interrupt
        >
            {url && <Suspense fallback={<Loader/>}>
                <Model refer={avatar} position={[0, -8.4, 0]} scale={14} url={url}/>
                <ambientLight/>
                <pointLight position={[10, 10, 10]}/>
            </Suspense>}
        </Canvas>
    </div>);
}
